<template>
  <el-container>
    <el-header>
      <Header/>
    </el-header>
    <el-container>
      <el-aside>
        <NavMenu/>
      </el-aside>
      <el-main>
        <router-view/>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import Header from '@/components/Header';
import NavMenu from '@/components/NavMenu';

export default {
  name: 'Layout',
  components: {
    Header,
    NavMenu
  }
}
</script>

<style lang="scss" scoped>
  .el-header {
    background-color: #363B40;
    color: #B8BFC6;
    text-align: left;
    line-height: 60px;
  }

  
  .el-aside {
    width: 270px !important;
    text-align: left;
  }

  .el-main {
    background-color: #363B40;
  }
</style>


