<template>
  <body class='typora-export os-windows' >
    <div  id='write'  class = 'is-node'><h1><a name="jdbc" class="md-header-anchor"></a><span>JDBC</span></h1><h2><a name="什么是-jdbc" class="md-header-anchor"></a><span>什么是 JDBC？</span></h2><p><span>		</span><span>JDBC（Java Data Base Connectivity，Java 数据库连接），是一种用于执行 SQL 语句的 Java API，可以为多种关系型数据库提供统一访问，它由一组用Java语言编写的类和接口组成。JDBC 提供了一种基准，据此可以构建更高级的工具和接口，使数据库开发人员能够编写数据库应用程序。</span></p><h2><a name="数据库驱动" class="md-header-anchor"></a><span>数据库驱动</span></h2><p><span>		</span><span>我们安装好数据库之后，我们的应用程序也是不能直接使用数据库的，必须要通过相应的数据库驱动程序，通过驱动程序去和数据库打交道。其实也就是数据库厂商的 JDBC 接口实现，即对 Connection 等接口的实现类的jar文件。</span></p><p><img src="@/assets/数据库驱动.png" referrerpolicy="no-referrer" alt="数据库驱动"></p><h2><a name="常用接口" class="md-header-anchor"></a><span>常用接口</span></h2><h4><a name="1driver接口" class="md-header-anchor"></a><span>1.Driver接口</span></h4><p><span>　　Driver 接口由数据库厂家提供，作为 java 开发人员，只需要使用 Driver 接口就可以了。在编程中要连接数据库，必须先装载特定厂商的数据库驱动程序，不同的数据库有不同的装载方法。如：</span></p><p><span>　　装载 MySql 驱动：Class.forName(&quot;com.mysql.jdbc.Driver&quot;);</span></p><p><span>　　装载 Oracle 驱动：Class.forName(&quot;oracle.jdbc.driver.OracleDriver&quot;);</span></p><h4><a name="2connection接口" class="md-header-anchor"></a><span>2.Connection接口</span></h4><p><span>　　Connection 与特定数据库的连接（会话），在连接上下文中执行 sql 语句并返回结果。</span><code>DriverManager.getConnection(url, user, password)</code><span>方法建立在 JDBC URL中定义的数据库 Connection 连接上。</span></p><p><span>　　连接 MySql 数据库：</span><code>Connection conn = DriverManager.getConnection(&quot;jdbc:mysql://host:port/database&quot;, &quot;user&quot;, &quot;password&quot;);</code></p><p><span>　　连接 Oracle 数据库：</span><code>Connection conn = DriverManager.getConnection(&quot;jdbc:oracle:thin:@host:port:database&quot;, &quot;user&quot;, &quot;password&quot;);</code></p><p><span>　　连接 SqlServer 数据库：</span><code>Connection conn = DriverManager.getConnection(&quot;jdbc:microsoft:sqlserver://host:port; DatabaseName=database&quot;, &quot;user&quot;, &quot;password&quot;);</code></p><p><span>　　</span><strong><span>常用方法：</span></strong></p><ul><li><span>createStatement()：创建向数据库发送sql的statement对象。</span></li><li><span>prepareStatement(sql) ：创建向数据库发送预编译sql的PrepareSatement对象。</span></li><li><span>prepareCall(sql)：创建执行存储过程的callableStatement对象。</span></li><li><span>setAutoCommit(boolean autoCommit)：设置事务是否自动提交。</span></li><li><span>commit() ：在链接上提交事务。</span></li><li><span>rollback() ：在此链接上回滚事务。</span></li></ul><h4><a name="3statement接口" class="md-header-anchor"></a><span>3.Statement接口</span></h4><p><span>　　用于执行静态SQL语句并返回它所生成结果的对象。</span></p><p><span>　　</span><strong><span>三种 Statement 类：</span></strong></p><ul><li><span>Statement：由 createStatement 创建，用于发送简单的SQL语句（不带参数）。</span></li><li><span>PreparedStatement ：继承自 Statement 接口，由 preparedStatement 创建，用于发送含有一个或多个参数的 SQL 语句。PreparedStatement 对象比 Statement 对象的效率更高，并且可以防止 SQL 注入，所以我们一般都使用PreparedStatement。</span></li><li><span>CallableStatement：继承自 PreparedStatement 接口，由方法 prepareCall 创建，用于调用存储过程。</span></li></ul><p><span>　　</span><strong><span>常用 Statement 方法：</span></strong></p><ul><li><span>execute(String sql):运行语句，返回是否有结果集</span></li><li><span>executeQuery(String sql)：运行select语句，返回ResultSet结果集。</span></li><li><span>executeUpdate(String sql)：运行insert/update/delete操作，返回更新的行数。</span></li><li><span>addBatch(String sql) ：把多条sql语句放到一个批处理中。</span></li><li><span>executeBatch()：向数据库发送一批sql语句执行。</span></li></ul><h4><a name="4resultset接口" class="md-header-anchor"></a><span>4.ResultSet接口</span></h4><p><span>　　</span><strong><span>ResultSet 提供检索不同类型字段的方法，常用的有：</span></strong></p><ul><li><span>getString(int index)、getString(String columnName)：获得在数据库里是 varchar、char 等类型的数据对象。</span></li><li><span>getFloat(int index)、getFloat(String columnName)：获得在数据库里是 Float 类型的数据对象。</span></li><li><span>getDate(int index)、getDate(String columnName)：获得在数据库里是 Date 类型的数据。</span></li><li><span>getBoolean(int index)、getBoolean(String columnName)：获得在数据库里是 Boolean 类型的数据。</span></li><li><span>getObject(int index)、getObject(String columnName)：获取在数据库里任意类型的数据。</span></li></ul><p><span>　　</span><strong><span>ResultSet还提供了对结果集进行滚动的方法：</span></strong></p><ul><li><span>next()：移动到下一行</span></li><li><span>Previous()：移动到前一行</span></li><li><span>absolute(int row)：移动到指定行</span></li><li><span>beforeFirst()：移动 resultSet 的最前面。</span></li><li><span>afterLast() ：移动到 resultSet 的最后面。</span></li></ul><p><span>使用后依次关闭对象及连接：ResultSet → Statement → Connection</span></p><pre spellcheck="false" class="md-fences md-end-block ty-contain-cm modeLoaded" lang="java" style="break-inside: unset;"><div class="CodeMirror cm-s-inner CodeMirror-wrap" lang="java"><div style="overflow: hidden; position: relative; width: 3px; height: 0px; top: 0px; left: 4px;"><textarea autocorrect="off" autocapitalize="off" spellcheck="false" tabindex="0" style="position: absolute; bottom: -1em; padding: 0px; width: 1000px; height: 1em; outline: none;"></textarea></div><div class="CodeMirror-scrollbar-filler" cm-not-content="true"></div><div class="CodeMirror-gutter-filler" cm-not-content="true"></div><div class="CodeMirror-scroll" tabindex="-1"><div class="CodeMirror-sizer" style="margin-left: 0px; margin-bottom: 0px; border-right-width: 0px; padding-right: 0px; padding-bottom: 0px;"><div style="position: relative; top: 0px;"><div class="CodeMirror-lines" role="presentation"><div role="presentation" style="position: relative; outline: none;"><div class="CodeMirror-measure"><span><span>​</span>x</span></div><div class="CodeMirror-measure"></div><div style="position: relative; z-index: 1;"></div><div class="CodeMirror-code" role="presentation" style=""><div class="CodeMirror-activeline" style="position: relative;"><div class="CodeMirror-activeline-background CodeMirror-linebackground"></div><div class="CodeMirror-gutter-background CodeMirror-activeline-gutter" style="left: 0px; width: 0px;"></div><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"><span class="cm-keyword">package</span> <span class="cm-def">com</span>.<span class="cm-