 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-string">"select user_id, account, password "</span> <span class="cm-operator">+</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-string">"from user "</span> <span class="cm-operator">+</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-string">"where 1 = 1"</span>;</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"><span cm-text="">​</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"><span class="cm-keyword">public</span> <span class="cm-variable">List</span><span class="cm-operator">&lt;</span><span class="cm-variable">User</span><span class="cm-operator">&gt;</span> <span class="cm-def">queryAllUser</span>() {</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-comment">// 开启连接</span></span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-variable">Connection</span> <span class="cm-variable">connection</span> <span class="cm-operator">=</span> <span class="cm-variable">DBUtil</span>.<span class="cm-variable">connectionHelper</span>();</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-variable">PreparedStatement</span> <span class="cm-variable">preparedStatement</span> <span class="cm-operator">=</span> <span class="cm-atom">null</span>;</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-variable">ResultSet</span> <span class="cm-variable">resultSet</span> <span class="cm-operator">=</span> <span class="cm-atom">null</span>;</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-keyword">try</span> {</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable">preparedStatement</span> <span class="cm-operator">=</span> <span class="cm-variable">connection</span>.<span class="cm-variable">prepareStatement</span>(<span class="cm-variable">SQL_QUERY_ALL_USER</span>);</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable">resultSet</span> <span class="cm-operator">=</span> <span class="cm-variable">preparedStatement</span>.<span class="cm-variable">executeQuery</span>();</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-keyword">while</span> (<span class="cm-variable">resultSet</span>.<span class="cm-variable">next</span>()) {</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable-3">Integer</span> <span class="cm-variable">userId</span> <span class="cm-operator">=</span> <span class="cm-variable">resultSet</span>.<span class="cm-variable">getInt</span>(<span class="cm-string">"user_id"</span>);</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable-3">String</span> <span class="cm-variable">account</span> <span class="cm-operator">=</span> <span class="cm-variable">resultSet</span>.<span class="cm-variable">getString</span>(<span class="cm-string">"account"</span>);</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable-3">String</span> <span class="cm-variable">password</span> <span class="cm-operator">=</span> <span class="cm-variable">resultSet</span>.<span class="cm-variable">getString</span>(<span class="cm-string">"password"</span>);</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable">resList</span>.<span class="cm-variable">add</span>(<span class="cm-keyword">new</span> <span class="cm-variable">UserDTO</span>(<span class="cm-variable">userId</span>, <span class="cm-variable">account</span>, <span class="cm-variable">password</span>));</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp;  }</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-keyword">return</span> <span class="cm-variable">resList</span>;</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  } <span class="cm-keyword">catch</span> (<span class="cm-variable">SQLException</span> <span class="cm-variable">e</span>) {</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable">e</span>.<span class="cm-variable">printStackTrace</span>();</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  } <span class="cm-keyword">finally</span> {</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp; &nbsp; &nbsp;<span class="cm-variable">DBUtil</span>.<span class="cm-variable">releaseResource</span>(<span class="cm-variable">connection</span>, <span class="cm-variable">statement</span>, <span class="cm-variable">resultSet</span>);</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  }</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp; &nbsp;<span class="cm-keyword">return</span> <span class="cm-atom">null</span>;</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;">}</span></pre></div></div></div></div></div><div style="position: absolute; height: 0px; width: 1px; border-bottom: 0px solid transparent; top: 702px;"></div><div class="CodeMirror-gutters" style="display: none; height: 702px;"></div></div></div></pre><h2><a name="事务" class="md-header-anchor"></a><span>事务</span></h2><h3><a name="基本概念" class="md-header-anchor"></a><span>基本概念</span></h3><p><span>		</span><span>一组要么同时执行成功，要么同时执行失败的 SQL 语句。是数据库操作的一个执行单元。</span></p><h3><a name="事务四大基本原则--acid" class="md-header-anchor"></a><span>事务四大基本原则  ACID</span></h3><h4><a name="a-atomicity原子性-操作这些指令时要么全部执行成功要么全部不执行只要其中一个指令执行失			败所有的指令都执行失败数据进行回滚-rollback-回到执行指令前的数据状态" class="md-header-anchor"></a><span>		</span><span>A: atomicity(原子性)， 操作这些指令时，要么全部执行成功，要么全部不执行。只要其中一个指令执行失</span><span>			</span><span>败，所有的指令都执行失败，数据进行回滚 rollback 回到执行指令前的数据状态。</span></h4><h4><a name="c-consistency一致性事务的执行使数据从一个状态转换为另一个状态但是对于整个数据的完整性保			持稳定" class="md-header-anchor"></a><span>		</span><span>C: consistency(一致性)，事务的执行使数据从一个状态转换为另一个状态，但是对于整个数据的完整性保</span><span>			</span><span>持稳定。</span></h4><h4><a name="i-isolation隔离性在该事务执行的过程中无论发生的任何数据的改变都应该只存在于该事务中对外			界不存在任何影响只有在事务确定正确提交之后才会显示该事务对数据的改变其它事务才能获取			到这些改变后的数据" class="md-header-anchor"></a><span>		</span><span>I: isolation(隔离性)，在该事务执行的过程中，无论发生的任何数据的改变都应该只存在于该事务中，对外</span><span>			</span><span>界不存在任何影响，只有在事务确定正确提交之后，才会显示该事务对数据的改变，其它事务才能获取</span><span>			</span><span>到这些改变后的数据。</span></h4><h4><a name="d-durability持久性当事务正确完成后它对于数据的改变是永久性的" class="md-header-anchor"></a><span>		</span><span>D: durability(持久性)，当事务正确完成后，它对于数据的改变是永久性的。</span></h4><h3><a name="事务的隔离级别" class="md-header-anchor"></a><span>事务的隔离级别</span></h3><h4><a name="由低到高" class="md-header-anchor"></a><span>由低到高</span></h4><ul><li><span>read uncommitted 读未提交</span></li><li><span>read committed 读提交</span></li><li><span>repeatable read 可重复读</span></li><li><span>serializable 序列化</span></li></ul><h4><a name="脏读" class="md-header-anchor"></a><span>脏读：</span></h4><p><span>		</span><span>当一个事务正在访问数据，并且对数据进行了修改，而且没有提交到数据库，这时另外一个事务也访问这个数据，然后使用了该数据。</span></p><h4><a name="不可重复读" class="md-header-anchor"></a><span>不可重复读：</span></h4><p><span>		</span><span>在一个事务内，多次读取同一数据，在这个事务还没有结束时，另一个事务也访问该同一数据。那么在第一个事务中的两次读取数据之间，由于第二个事务的修改，那么第一个事务两次读到的数据不一样。</span></p><h4><a name="幻读" class="md-header-anchor"></a><span>幻读：</span></h4><p><span>		</span><span>当事务不是独立执行时发生的一种现象，例如第一个事务对一个表中的数据进行了修改，这种修改涉及到表中</span><strong><span>全部数据行</span></strong><span>，同时第二个事务也修改这个表中的数据，这种修改是向表中插入一条新数据。那么就会发生操作第一个事务的用户发现表中还有</span><strong><span>没有修改的数据行</span></strong><span>。</span></p><p><strong><span>√ 表示可能出现</span></strong></p><p><strong><span>× 不会出现</span></strong></p><figure><table><thead><tr><th style='text-align:center;' ><span>隔离级别</span></th><th style='text-align:center;' ><span>脏读</span></th><th style='text-align:center;' ><span>不可重复读</span></th><th style='text-align:center;' ><span>幻读</span></th></tr></thead><tbody><tr><td style='text-align:center;' ><span>read uncommitted</span></td><td style='text-align:center;' ><span>√</span></td><td style='text-align:center;' ><span>√</span></td><td style='text-align:center;' ><span>√</span></td></tr><tr><td style='text-align:center;' ><span>read committed</span></td><td style='text-align:center;' ><span>×</span></td><td style='text-align:center;' ><span>√</span></td><td style='text-align:center;' ><span>√</span></td></tr><tr><td style='text-align:center;' ><span>repeatable read</span></td><td style='text-align:center;' ><span>×</span></td><td style='text-align:center;' ><span>×</span></td><td style='text-align:center;' ><span>√</span></td></tr><tr><td style='text-align:center;' ><span>serializable</span></td><td style='text-align:center;' ><span>×</span></td><td style='text-align:center;' ><span>×</span></td><td style='text-align:center;' ><span>×</span></td></tr></tbody></table></figure><p>&nbsp;</p></div>
  </body>
</template>

