<template>
  <el-row>
    <el-col :span="24">
      <el-menu
        default-active="1-1"
        @open="handleOpen"
        @close="handleClose"
        @select="handleSelect"
        background-color="#363B40"
        text-color="#B8BFC6"
        active-text-color="#DA924A">
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span>Java 基础</span>
          </template>
          <el-menu-item index="1-1">
            数据类型
          </el-menu-item>
          <el-menu-item index="1-2">
            基础语法
          </el-menu-item>
          <el-menu-item index="1-3">
            JDBC
          </el-menu-item>
        </el-submenu>
        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span>ORM</span>
          </template>
          <el-menu-item index="2-1">
            MyBatis
          </el-menu-item>
          <el-menu-item index="2-2">
            Hibrnate
          </el-menu-item>
          <el-menu-item index="2-3">
            JPA
          </el-menu-item>
        </el-submenu>
        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span>Spring 基础</span>
          </template>
          <el-menu-item index="3-1">
            Spring
          </el-menu-item>
          <el-menu-item index="3-2">
            Spring MVC
          </el-menu-item>
          <el-menu-item index="3-3">
            Spring AOP
          </el-menu-item>
          <el-menu-item index="3-4">
            Spring Boot
          </el-menu-item>
          <el-menu-item-group title="Spring Cloud" index="3-5">
            <el-menu-item index="3-5-1">
              Spring Cloud Netflix Eureka
            </el-menu-item>
            <el-menu-item index="3-5-2">
              Spring Cloud Netflix Zuul
            </el-menu-item>
            <el-menu-item index="3-5-3">
              Spring Cloud Gateway
            </el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-submenu index="4">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span>设计模式</span>
          </template>
          <el-menu-item index="4-1">
            单例设计模式
          </el-menu-item>
        </el-submenu>  
        <el-submenu index="5">
          <template slot="title">
            <i class="el-icon-menu"></i>
            <span>配置中心</span>
          </template>
          <el-menu-item index="5-1">
            携程 Apollo
          </el-menu-item>
        </el-submenu>  
      </el-menu>
    </el-col>
  </el-row>
</template>

<script>
import router from '@/router';
export default {
  name: 'NavMenu',
  mounted() {
    router.push('/java/datatype');
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    handleSelect(index, indexPath) {
      switch(index) {
        case '1-1':
          router.push('/java/datatype');
          break;
        case '1-2':
          router.push('/java/basic_grammar');
          break;
        case '1-3':
          router.push('/java/jdbc');
          break;
        case '2-1':
          router.push('/orm/mybatis');
          break;
        case '2-2':
          router.push('/orm/hibernate');
          break;
        case '2-3':
          router.push('/orm/jpa');
          break;
        case '3-1':
          router.push('/framework/spring');
          break;
        case '3-2':
          router.push('/framework/spring_mvc');
          break;
        case '3-3':
          router.push('/framework/spring_aop');
          break;
        case '3-4':
          router.push('/framework/spring_boot');
          break;
        case '3-5-1':
          router.push('/framework/spring_cloud_netflix_eureka');
          break;
        case '3-5-2':
          router.push('/framework/spring_cloud_netflix_zuul');
          break;
        case '3-5-3':
          router.push('/framework/spring_cloud_gateway');
          break;  
        case '4-1':
          router.push('/design_patterns/singleton');
          break; 
        case '5-1':
          router.push('/propcenter/apollo');
          break;   
      }
      
    }
  }
}
</script>

<style lang="scss" scoped>
   .el-row {
     width: 270px !important;
     .el-col {
       width: 270px;
     }
     .el-menu {
       /* 解决左侧菜单突出 1px 问题 */
       border-right-width: 0;
       height: calc(100vh - 60px) !important;
       :hover {
          color: #FFFFFF !important;
        }
     }
   }
</style>


