import HelloWorld from '@/components/HelloWorld';
import Layout from '@/components/Layout';
import Singleton from '@/components/article/dp/Singleton';
import DataType from '@/components/article/java/DataType';
import BasicGrammar from '@/components/article/java/BasicGrammar';
import Jdbc from '@/components/article/java/Jdbc';
import MyBatis from '@/components/article/orm/MyBatis';
import Hibernate from '@/components/article/orm/Hibernate';
import Jpa from '@/components/article/orm/Jpa';
import Spring from '@/components/article/framework/Spring';
import SpringMvc from '@/components/article/framework/SpringMvc';
import SpringAop from '@/components/article/framework/SpringAop';
import SpringBoot from '@/components/article/framework/SpringBoot';
import SpringCloudNetflixEureka from '@/components/article/framework/SpringCloudNetflixEureka';
import SpringCloudNetflixZuul from '@/components/article/framework/SpringCloudNetflixZuul';
import SpringCloudGateway from '@/components/article/framework/SpringCloudGateway';
import Apollo from '@/components/article/propcenter/Apollo';

export const Map = [
  {
      path: '/',
      name: 'Layout',
      component: Layout,
      children: [
        
      ]  
  },
  {
    path: '/java/datatype',
    name: 'DataType',
    component: DataType
  },
  {
    path: '/java/basic_grammar',
    name: 'BasicGrammar',
    component: BasicGrammar
  },
  {
    path: '/java/jdbc',
    name: 'Jdbc',
    component: Jdbc
  },
  {
    path: '/orm/mybatis',
    name: 'MyBatis',
    component: MyBatis
  },
  {
    path: '/orm/hibernate',
    name: 'Hibernate',
    component: Hibernate
  },
  {
    path: '/orm/jpa',
    name: 'Jpa',
    component: Jpa
  },
  {
    path: '/framework/spring',
    name: 'Spring',
    component: Spring
  },
  {
    path: '/framework/spring_mvc',
    name: 'SpringMvc',
    component: SpringMvc
  },
  {
    path: '/framework/spring_aop',
    name: 'SpringAop',
    component: SpringAop
  },
  {
    path: '/framework/spring_boot',
    name: 'SpringBoot',
    component: SpringBoot
  },
  {
    path: '/framework/spring_cloud_netflix_eureka',
    name: 'SpringCloudNetflixEureka',
    component: SpringCloudNetflixEureka
  },
  {
    path: '/framework/spring_cloud_netflix_zuul',
    name: 'SpringCloudNetflixZuul',
    component: SpringCloudNetflixZuul
  },
  {
    path: '/framework/spring_cloud_gateway',
    name: 'SpringCloudGateway',
    component: SpringCloudGateway
  },
  {
    path: '/design_patterns/singleton',
    name: 'Singleton',
    component: Singleton
  },
  {
    path: '/propcenter/apollo',
    name: 'Apollo',
    component: Apollo
  },
]


